@import './src/styles/variables';
.header{
    align-items: center;
    background: #000000;
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
    height: 80px;
    justify-content: flex-end;
    padding: 0 30px;
    .headerNav{
        align-items: stretch;
        display: flex;
        flex-direction: row;
        flex-wrap: nowrap;
        justify-content: space-between;
        width: 340px;
        .headerLinks{
            position: relative;
            color: #fff;
            cursor: pointer;
            font-family: Josefin Sans,sans-serif;
            font-size: 16px;
            font-weight: 400;
            line-height: 1.2;
            &:before {
                content: '';
                background-color: hsla(200, 61%, 26%, 0.75);
                position: absolute;
                left: 0;
                bottom: -5px;
                width: 0%;
                height: 8px;
                z-index: -1;
                transition: all .3s ease-in-out;
            }
            &:hover:before {
                z-index: 0;
                width: 100%;
            }
            &:hover{
                color: $lightSecondColor;
            }

        }
    }
}

